body {
  font-family: sans-serif;
  margin: 20px;
  padding: 0;
}

.square {
  background: #f0cfa7;
  /* background: white; */
  border: 1px solid #999;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 34px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 34px;
}

.board-row:after {
  clear: both;
  content: '';
  display: table;
}

.left-text {
  margin-bottom: 10px;
  text-align: left;
}
.status{
  color: rgb(123, 67, 132);
  font-size: 20px;
}
.play-button{
  color: rgb(118, 37, 119);
  background-color: rgb(225, 228, 166);
  margin: 10px;
  font-size: 20px;
}